.featured {
  width: 100%;
  max-width: 1024px;
  display: flex;
  justify-content: space-between;
  gap: 20px;
  z-index: 1;
}
.featuredItems {
  cursor: pointer;
  position: relative;
  color: white;
  border-radius: 20px;
  overflow: hidden;
  height: 300px;
}
.featuredImg {
  width: 320px;
  height: 320px;
  object-fit: cover;
}

.featuredTitles {
  position: absolute;

  font-weight: bold;
  bottom: 20px;
  left: 20px;
}

.featuredTitles h1 {
  font-weight: 800;
  margin-bottom: -10px;
}
