.header{
    background-color: #003580;
    color: #fff;
    display: flex;
    justify-content: center;
    position: relative;
}

.headerContainer{
    width: 100%;
    max-width: 1024px;
    margin: 20px 0px 100px 0px;
}

.headerContainer.listMode{
    width: 100%;
    max-width: 1024px;
    margin: 20px 0px 0px 0px;
}

.headerList{
    display:flex;
    gap: 40px;
}

.headerListItems{
    display:flex;
    align-items: center;
    gap:10px;
    margin-bottom: 40px;
}

.headerListItems.active{
    border: 1px solid #fff;
    padding: 10px;
    border-radius: 20px;
}

.headerDesc{
    margin: 20px 0px;
}

.headerbtn{
    background-color: #0071c2;
    border: none;
    padding: 10px;
    cursor: pointer;
    color: #fff;
}

.headerSearch{
    height: 30px;
    background-color: #fff;
    border:3px solid #febb02;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 10px 0px;
    border-radius: 5px;
    position: absolute;
    bottom: -25px;
    width: 100%;
    max-width: 1024px;

}

.headerIcon{
    color: lightgray;
  
}

.headerSearchInput{
    border: none;
    outline: none;
}

.headerSearchItems{
    display: flex;
    align-items: center;
    gap:10px;
}

.date{
    position: absolute;
    top: 50px;
    z-index: 2;
}

.headerTextInput{
    color: lightgray;
}

.options{
    position: absolute;
    top:50px;
    background-color: #fff;
    color: grey;
    border-radius: 5px;
    box-shadow: 0px 0px 10px -5px black;
    z-index: 2;
}   

.optionsItems{
    width: 200px;
    display: flex;
    justify-content: space-around;
}

.optionTab{
    display: flex;
    align-items: center;
    justify-items: center  ;
    gap: 10px;
    font-size: 12px;
    color: black;
    padding: 5px;
}

.optionCounterBtn{
    width: 30px;
    height: 30px;
    border:1px solid #0071c2;
    color:#0071c2;
    cursor:pointer;
    background-color: #fff;
}
.optionCounterBtn:disabled{
    cursor:not-allowed;
}