.login {
  width: 100%;
  height: 100%;
}
.lContainer {
  margin: 10% auto;
  width: fit-content;
  padding: 1em;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.lInput {
  padding: 0.9em;
  outline: none;
  border-radius: 4px;
  border: 2px solid darkgray;
}
.lButton {
  padding: 0.9em;
  outline: none;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background: #0071c2;
  color: #fff;
  font-weight: bold;
}
.lContainer > span {
  text-align: center;
}
