.rContainer {
  margin: 10% auto;
  width: fit-content;
  padding: 1em;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.rContainer .rInput {
  width: fit-content;
  padding: 0.9em;
  outline: none;
  border-radius: 4px;
  border: 2px solid darkgray;
}
.rButton {
  padding: 0.9em;
  outline: none;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background: #0071c2;
  color: #fff;
  font-weight: bold;
}
